import React, {useState} from "react";
import "./pages.css"
import {Form,Button} from 'react-bootstrap'

function Calc2 (){

    const [pais2, setpais] = useState("")
    const [casos2, setcasos] = useState("")
    const [dias2, setdias] = useState("")
    const [prediccion2, setprediccion] = useState("")
    const [respuestaa, setrespuesta] = useState("Ingrese la informacion para predecir")
    const [grafica2, setgrafica] = useState();
    const [cpais2, setcpais] = useState("");
    // Para el manejo de archivo
    const [contenidoArchvio, setcontenido]= useState("")
    const [tipoarchivo, setipoarchivo] = useState("")

    async function getgrafica (event){
      event.preventDefault()
      const response = await fetch("https://powerful-tundra-15123.herokuapp.com/plot.png");
      const data = await response.blob()
      const imageObjectUrl = URL.createObjectURL(data)
      setgrafica(imageObjectUrl)
      
    }
  
    const handleClick =(event) =>{
      event.preventDefault();
      const options ={
        method:"POST",
        body:JSON.stringify({
          reporte: 2,
          content: contenidoArchvio,
          tipoa: tipoarchivo,
          dias:dias2,
          pais:pais2,
          casos: casos2,
          predic: prediccion2,
          cpais: cpais2
          
        })
      };
     
  
      fetch('https://powerful-tundra-15123.herokuapp.com/reportes',options)
      .then(resp =>{
        if(resp.status ===200) return resp.json();
        else alert("Si sale esto. Creo que no ganaré compi :C")
      })
      .then(data =>{
        setrespuesta("Los infectados serán " + data.Reporte)
      
      })
      .catch(error =>{
        console.error("Hubo un error!!!",error)
      })
    }

    async function handleFile(event){
        event.preventDefault();
        var file = event.target.files[0]
        setipoarchivo(file.name.split(".")[1])
        var reader = new FileReader();
        reader.onload = function(event) {
          setcontenido(event.target.result)
        }
        reader.readAsText(file)      
      }

      

    return(
      <div className="titulo">
            <strong className="Calc">Predicción de Infectados en un País..</strong>
            <div className="row no-gutters">
        <div className = "col no-gutters">

          <div className = "leftside">
            <Form>
            <h3>Carga de archivo</h3>
            <Form.Group controlId="formFile" className="mb-3">
              <Form.Label>Selecciona un archivo .csv, .xsls</Form.Label>
              <Form.Control type="file" name="file" onChange={(e)=> handleFile(e)}/>
              </Form.Group>
              <h3>Parametrizacion</h3>
              <Form.Group>
              <Form.Label>Selecciona columna para Pais</Form.Label>
              <Form.Control name="cpais" placeholder="Columna para Pais" value={cpais2} onChange={(e)=> setcpais(e.target.value)}></Form.Control>
              <Form.Label>Selecciona columna para Casos</Form.Label>
              <Form.Control name="casos" placeholder="Columna para Casos" value={casos2} onChange={(e)=> setcasos(e.target.value)}></Form.Control>
              <Form.Label>Selecciona columna para Fecha</Form.Label>
              <Form.Control name="dias" placeholder="Columna para Fecha" value={dias2} onChange={(e)=> setdias(e.target.value)}></Form.Control>
              <h3>Datos</h3>
              <Form.Label>País a consultar</Form.Label>
              <Form.Control name="pais" placeholder="Ingrese el pais a consultar" value={pais2} onChange={(e)=> setpais(e.target.value)}></Form.Control>
              <Form.Label>Fecha a predecir</Form.Label>
              <Form.Control name="prediccion" placeholder="Ingrese fecha a predecir YYYY-MM-DD" value={prediccion2} onChange={(e)=> setprediccion(e.target.value)}></Form.Control>
              </Form.Group>
              
              <br></br>
              <Button className="botones" onClick={handleClick}>Consultar</Button>

            </Form>
         
          </div>
        </div>
        <div className ="col no-gutters">
          <div className = "rightside">
          
          <h3>Reporte</h3>
          <img  src={grafica2}/>  
          <br></br>
          <Button className="botones" onClick={getgrafica}>Mostrar Grafica</Button>
          <br></br>
          <h3>{respuestaa}</h3>
     
          <br></br>
          <Button className="botones">Descargar PDF</Button>
          </div>
        </div>

    </div>
            
        </div>
    );

}

export default Calc2;